
@import url('https://fonts.googleapis.com/css2?family=Archivo+Black&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Mulish&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Archivo+Black&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Scheherazade+New:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Varela+Round&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cairo&display=swap');


nav{
 padding: 25px 0px;
 display: flex;
 justify-content: space-between;
 align-items: center;
 position: relative;
}

nav h1{
  font-family: 'Archivo Black', sans-serif;
  color: white;
  font-size: 35px;
  cursor: default;
  margin-left: 25px;
}

nav ul{
  list-style: none;
  padding: 0;
  margin-top: 40px;
  margin-right: 25px;
}

nav ul li{
  display: inline-block;
  padding: 0px 10px;
  position: relative;
  margin: 0px 8px;
  height: 40px;
}


@keyframes NavAni {
  0%{
    width: 0%;
  }
  25%{
    width: 25%;
  }
  50%{
    width: 50%;
  }
  75%{
    width: 75%;
  }
  100%{
    width: 100%;
  }
}


@keyframes NoAni {
  0%{
    width: 100%;
  }
  25%{
    width: 75%;
  }
  50%{
    width: 50%;
  }
  75%{
    width: 25%;
  }
  100%{
    width: 0%;
  }
}

#NavLi::after{
  animation: NoAni .5s linear forwards;
  -webkit-animation: NoAni .5s linear forwards;
  content: '';
  position: absolute;
  height: 2px;
  background-color: yellow;
  bottom: 0;
  width: 0%;
  right: 0;
}

nav ul li:hover #NavLi::after{
  content: '';
  position: absolute;
  height: 2px;
  background-color: yellow;
  bottom: 0;
  width: 0%;
  right: 0;
  animation: NavAni .5s linear forwards;
  -webkit-animation: NavAni .5s linear forwards;
 
}

nav ul li a{
 text-decoration: none;
 font-family: 'Mulish', sans-serif;
 color: white;
 font-size: 17px;

}

nav ul li a:hover{
  color: white;
}



.Bars{
  position: absolute;
  right: 0;
  cursor: pointer;
  display: none;
}

.Bars i{
  color: white;
  font-size: 23px;
  padding: 8px 15px;
  margin-right: 20px;
  border: 1px solid white;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  
}


@keyframes SecondAni {
  0%{
    left: 470px;
    opacity: 0;
  }

  25%{
    left: 352.5px;
    opacity: .3;
  }

  50%{
    left: 235px;
    opacity: .5;
  }

  75%{
    left: 117.5px;
    opacity: .8;
  }

  100%{
    left: 0;
   opacity: 1;
  }

}

@keyframes ThirdAni {
  0%{
    left: 0px;
    opacity: 0;
  }

  25%{
    left: 117.5px;
    opacity: .1;
  }

  50%{
    left: 235px;
    opacity: .4;
  }

  75%{
    left: 352.5px;
    opacity: .7;
  }

  100%{
    left: 470px;
    opacity: 1;
    
  }

}

.None{
  animation: ThirdAni .5s linear forwards;
  -webkit-animation: ThirdAni .5s linear forwards;
  left: 470px;
  position: absolute;
  width: 100%;
  opacity: 0;
  display: none;
}

.Divs{
 position: absolute;
 width: 100%;
 left:  470px;
 animation: SecondAni .4s linear forwards;
 -webkit-animation: SecondAni .4s linear forwards;
}

.SecondUl{
 list-style: none;
 padding: 20px 0px 10px 0px;
 background-color: #333333;
 margin-top: -20px;
 margin-right: 25px;
 display: flex;
 flex-direction: column;
 align-items: center;
 width: 100%;

}


.SecondUl li{
  
  padding: 0px 10px;
  position: relative;
  margin: 5px 0px;
  height: 40px;
}



.SecondUl li:hover #NavLi::after{
  content: '';
  position: absolute;
  height: 2px;
  background-color: yellow;
  bottom: 0;
  width: 0%;
  right: 0;
  animation: NavAni .5s linear forwards;
  -webkit-animation: NavAni .5s linear forwards;
}

.SecondUl li a{
  text-decoration: none;
  font-family: 'Mulish', sans-serif;
  color: white;
  font-size: 17px;
}


@media (max-width: 473px) {
  .Bars{
    display: block;
  }
  nav ul{
   display: none;
  }
}





.AfterNav{
  display: flex;
  justify-content: space-between;
  padding: 15px 0px 50px 0px;
  align-items: center;
  
}

.Center img{
  width: 300px;
}

.Center{
  background-color: #2B2B34;
  height: 350px;
  display: flex;
  align-items: center;
  width: 350px;
  justify-content: center;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  margin-left: 50px;
}

.Right {
  width: 400px;
  color: white;
  margin-right: 50px;
}

.Left{
  width: 300px;
  color: white;
  margin-left: 50px;
  position: relative;
}

.Left h1{
  font-family: 'Archivo Black', sans-serif;
  color: #DADADA;
  font-size: 55px;
  letter-spacing: 1px;
  line-height: 55px;
}

.Left h1 span{
  font-family: 'Scheherazade New', serif;
  font-size: 65px;
  color: #E4AA48;
}

.Left hr{
  width: 40px;
  position: absolute;
  left: 0;
  border: 1px solid #E4AA48;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}

.Icons{
  margin: 20px  0px 20px 0px;
}

.Icons a{
 font-size: 25px;
 margin: 0px 5px;
 color: white;
 transition: .3s all linear;
 -webkit-transition: .3s all linear;
 -moz-transition: .3s all linear;
 -ms-transition: .3s all linear;
 -o-transition: .3s all linear;
}

.Icons a:hover{
  color: #DADADA;
}

.Contacta{
  padding-top: 15px;
  
}

.Contacta a{
  color: white;
  text-decoration: none;
  border: 2px solid #E4AA48;
  padding: 10px 15px;
  font-family: 'Poppins', sans-serif;
  transition: .3s all linear;
  -webkit-transition: .3s all linear;
  -moz-transition: .3s all linear;
  -ms-transition: .3s all linear;
  -o-transition: .3s all linear;
}

.Contacta a:hover{
  background-color: #E4AA48;
}

.Right h3{
  color: #E4AA48;
  font-family: 'Roboto', sans-serif;
}

.Right h1{
  font-family: 'Archivo Black', sans-serif;
  color: #DADADA;
}

.Right p{
  font-family: 'Varela Round', sans-serif;
}

@media (max-width: 1150px) {
  .AfterNav{
    flex-wrap: wrap;
    justify-content: center;
    gap: 35px;
    text-align: center;
  }
  .Left{
    margin-left: 0px;
  }
  .Right{
    margin-right: 0px;
  }
  .Center{
    margin-left: 0px;
  }
}

