}

.SkillsDiv h1{
  font-family: 'Roboto', sans-serif;
  color: #E4AA48;
  margin: 15px 0px;
  text-align: center;
  
}

.Skills{
  margin: 25px 0px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

